.nav-bar-container {
  height: 100px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .nav-bar-container .nav-bar-branded-area {
    cursor: pointer; }
    .nav-bar-container .nav-bar-branded-area .name-text {
      color: white;
      text-decoration: none !important;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 45px; }
    .nav-bar-container .nav-bar-branded-area .title-text {
      text-transform: uppercase;
      color: white;
      font-weight: 200;
      font-size: 16px; }
  .nav-bar-container .nav-links-container {
    padding-right: 15px; }
    .nav-bar-container .nav-links-container #nav {
      display: inline-block;
      margin-top: 0px;
      margin-bottom: 0px;
      padding: 0; }
      .nav-bar-container .nav-links-container #nav .nav-link-li {
        list-style-type: none; }
      .nav-bar-container .nav-links-container #nav li {
        letter-spacing: 0.3px;
        text-decoration: none;
        display: inline-block; }
      .nav-bar-container .nav-links-container #nav .nav-slider {
        height: 0.2rem;
        margin-top: 3px;
        background: #67ccc2;
        border: none;
        -webkit-transition: 0.3s ease-in-out;
        transition: 0.3s ease-in-out;
        display: block; }
    @media screen and (min-width: 768px) {
      .nav-bar-container .nav-links-container #nav hr {
        height: 0.2rem;
        margin-top: 3px;
        background: #67ccc2;
        border: none;
        -webkit-transition: 0.3s ease-in-out;
        transition: 0.3s ease-in-out;
        display: block; } }
    .nav-bar-container .nav-links-container .nav-link {
      display: inline-block;
      margin-right: 30px;
      color: white;
      cursor: pointer;
      text-transform: uppercase;
      font-size: 12pt; }
    .nav-bar-container .nav-links-container .contact-button {
      display: inline-block;
      padding: 5px;
      background-color: #131212;
      border: 2px solid #67ccc2;
      color: #67ccc2;
      font-family: IBM Plex Sans;
      font-weight: 600;
      cursor: pointer; }
      .nav-bar-container .nav-links-container .contact-button:hover {
        background-color: #67ccc2;
        color: white; }

